

.App {
 
  min-height: 100vh;
  display: flex;
  justify-content: center;
  background-color: rgb(146, 166, 227);
  font-family: 'Arial', sans-serif; 
    font-size: 16vh; 
    color: #ffffff;
    text-align: center; 
    text-shadow: 
        10px 8px 0 rgba(0, 0, 0, 0.3), 
        10px 8px 0 rgba(0, 0, 0, 0.2), 
        6px 6px 0 rgba(0, 0, 0, 0.1); 
    transform: translateZ(0);
    background: linear-gradient(90deg, #15bdeb, #447fc7, #0582ff);
    filter: drop-shadow(0 4px 15px rgba(0, 0, 0, 0.2));
}



.App-link {
  color: #61dafb;
}
